.videofooter{
    position: relative;
    bottom: 20%;
    color: white;
    margin-left: 30px;
}

.videofooter__text h3{
    padding-bottom: 20px;
}

.videofooter__text p{
    padding-bottom: 20px;
}

.videofooter__music{
    display: flex;
}

.videofootermusic__text{
    width: 80%;
    overflow-x: hidden;
}

.videofootermusic__text p{
    animation: moveTheText 3s infinite linear;
}

.videofooter__record{
    animation: spinTheRecord 3s infinite linear;
    position: absolute;
    bottom: 5px;
    right: 20px;
    height: 50px;
    filter: invert(1)
}

@keyframes spinTheRecord {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

@keyframes moveTheText {
    0%{
        transform: translateX(100%);
    }
    100%{
        transform: translateX(-100%);
    }
}

@media only screen and (max-width: 440px){
   .videofooter{
    bottom: 22%;
    margin-left: 10px;
   }
   .videofootermusic__text{
    width: 78%;
   }
  }